.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px 0px;
  gap: 64px;
  /* position: absolute; */
  width: 100%;
  height: 196px;
  /* left: 0px; */
  /* top: 892px; */
  margin-top: 5px;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 32px 0px 0px;
  gap: 16px;
  width: 91.4%;
  height: 132px;
  border-top: 1.3px solid rgba(185, 178, 178, 0.098);
}

@media screen and (min-width: 768px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 48px 0px;
    gap: 64px;
    position: absolute;
    width: 1440px;
    height: 160px;
    left: 0px;
    top: 998px;
  }
  .content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 32px 0px 0px;
    gap: 32px;
    width: 85%;
    height: 64px;
  }
}