@import url('https://fonts.cdnfonts.com/css/inter?styles=29142,29132,29135,29137,29140,29128');

* {
  font-family: 'Inter' sans-serif;
}
.layout {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 64px 0px;
  gap: 48px;
  height: 884px;
  left: 0px;
  top: 0px;
  margin: auto;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  width: 91.4%;
  margin: auto;
  height: 116px;
}

.header h1 {
  width: 343px;
  height: 44px;
  /* font-family: 'Inter' sans-serif; */
  font-style: normal;
  font-weight: 600;
  font-size: 3.6rem;
  line-height: 44px;
  letter-spacing: -0.02em;
  color: #101828;
}

.header p {
  /* font-family: 'Inter' sans-serif; */
  font-style: normal;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 28px;
  color: #475467;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
  margin: auto;
  gap: 32px;
  width: 100%;
  height: 592px;
}

form .name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px 0;
  gap: 24px;
  width: 91.4%;
  height: 160px;
}

form .name .row {
  width: 100%;
}

form .email, form .message {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 6px;
  width: 91.4%;
  height: 70px;
}

form div label {
  width: 100px;
  height: 20px;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 2rem;
  color: #344054;
}

form div input {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 14px;
  gap: 8px;
  width: 100%;
  height: 44px;
  background: #FFFFFF;
  border: 1px solid #D0D5DD;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
  border-radius: 8px;
}

form .text {
  height: 158px;
  width: 91.4%;
}

form div textarea {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 12px 14px;
  gap: 8px;
  width: 100%;
  min-height: 132px;
  background: #FFFFFF;
  border: 1px solid #D0D5DD;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
  border-radius: 8px;
  font-family: sans-serif;
}

.span {
  font-size: 1.3rem;
  display: block;
  margin-top: .5rem;
  color: rgba(255, 0, 0, 0.85);
}

.invalid{
  border: 2px solid rgba(225, 0, 0, 0.307);
  box-shadow: 0 0 5px rgba(255, 0, 0, 0.52);
}

form .check {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 12px;
  width: 91.4%;
  height: 48px;
}

form .check input {
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  background: #FFFFFF;
  border: 1px solid #D0D5DD;
  border-radius: 6px;
}

form .check label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 2px;
  width: 90.7%;
  height: 48px;
  font-size: 1.2rem;
  color: #475467;
}

button {
  margin: auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  gap: 8px;
  width: 91.4%;
  height: 48px;
  background: #1570EF;
  border: 1px solid #1570EF;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
  border-radius: 8px;
  color: #FFFFFF;
}



@media screen and (min-width: 768px) {
  .header {
    width: 720px;
    height: 94px;
  }

  .header h1 {
    width: 448px;
    height: 44px;
  }

  .header p {
    font-size: 2rem;
    line-height: 30px;
  }

  form {
    align-items: flex-start;
    gap: 32px;
    width: 720px;
    height: 474px;
  }

  form .name {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0px;
    gap: 24px;
    width: 720px;
    height: 70px;
  }

  form .name .row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 6px;
    width: 348px;
    height: 70px;
  }

  form .email {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 6px;
    width: 720px;
    height: 70px;
  }

  form .text {
    height: 158px;
    width: 720px;
  }

  form .check label {
    padding: 0px;
    gap: 2px;
    width: 688px;
    height: 24px;
    font-size: 1.6rem;
  }

  button {
    width: 720px;
  }
}